import { defineRouting } from 'next-intl/routing'
import { createNavigation } from 'next-intl/navigation'

export const routing = defineRouting({
  // Liste des locales supportes
  locales: ['fr', 'en'],

  // Locale par dfaut
  defaultLocale: 'fr',

  // Prfixe de locale : 'never' signifie qu'aucune locale n'a de prfixe dans l'URL
  // La locale est dtecte automatiquement ou via les cookies
  // Cette configuration vite les redirections vers des routes prfixes qui n'existent pas
  localePrefix: 'never',
})

// Crer les helpers de navigation
export const { Link, redirect, usePathname, useRouter } = createNavigation(routing)

