import { getRequestConfig } from 'next-intl/server'
import { cookies } from 'next/headers'
import { routing } from './routing'

export default getRequestConfig(async ({ requestLocale }) => {
  // Cette fonction sera appele pour chaque requte
  // requestLocale est fourni par le middleware next-intl (peut tre null si le middleware n'est pas appel)
  let locale = await requestLocale

  // Si requestLocale est null, essayer de rcuprer la locale depuis les cookies
  // Cela permet de grer les routes avec prfixe de locale (/en/* ou /fr/*) mme avec localePrefix: 'never'
  if (!locale) {
    try {
      const cookieStore = await cookies()
      const localeCookie = cookieStore.get('NEXT_LOCALE')?.value
      if (localeCookie && routing.locales.includes(localeCookie as any)) {
        locale = localeCookie
      }
    } catch (error) {
      // En cas d'erreur lors de la lecture des cookies, utiliser la locale par dfaut
      console.error('Error reading locale cookie:', error)
    }
  }

  // Assurer que la locale est valide
  // Si requestLocale est null/undefined ou n'est pas dans la liste des locales, utiliser la locale par dfaut
  // Cela garantit que la locale fonctionne mme si le middleware next-intl n'est pas appel pour certaines routes
  if (!locale || !routing.locales.includes(locale as any)) {
    locale = routing.defaultLocale
  }

  try {
    return {
      locale,
      messages: (await import(`../messages/${locale}.json`)).default,
    }
  } catch (error) {
    // En cas d'erreur lors du chargement des messages, utiliser la locale par dfaut
    console.error('Error loading messages for locale:', locale, error)
    return {
      locale: routing.defaultLocale,
      messages: (await import(`../messages/${routing.defaultLocale}.json`)).default,
    }
  }
})

